.container {
  margin: 0 auto;
  max-width: 1440px;
  padding: 0 30px;
    
  @include onTablet {
    padding: 0 34px;
  }

  @include onDesktop {
    padding: 0 134px;
  }

  &__grid {
    @include onTablet {
      padding: 0 34px;
    }
    
    @include onDesktop {
      padding: 0 134px;
    }
  }
}
